import * as React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SeoBusiness from "../components/seoBusiness"
import GoogleLogoIcon from "../images/svgs/google-logo.svg"
import FacebookLogoIcon from "../images/svgs/facebook-logo.svg"
import GoogleMapsLogoIcon from "../images/svgs/google-maps-logo.svg"

const StorePage = ({data}) => {
    const storeInfo = data.allMarkdownRemark.nodes[0].frontmatter
    return (
  <Layout>
    <SeoBusiness title="Sore/Hours" />
    <section className="page-section cta">
            <div className="container">
                <div className="row">
                    <div className="col-xl-9 mx-auto">
                        <div className="cta-inner bg-faded text-center rounded">
                            <h2 className="section-heading mb-5">
                                <span className="section-heading-upper">{storeInfo.subtitle}</span>
                                <span className="section-heading-lower">{storeInfo.title}</span>
                            </h2>
                            <ul className="list-unstyled list-hours mb-5 text-left mx-auto">
                              {storeInfo.hours && storeInfo.hours.map( hour => (
                                <li className="list-unstyled-item list-hours-item d-flex">
                                  {hour.day}
                                  <span className="ms-auto">{hour.time}</span>
                                </li>
                              ))}
                            </ul>
                            <p>
                              <strong> Winter hours:</strong> 
                              <br />
                              <em>Sunday appoinment only</em>
                              <br />
                              All other days: {storeInfo.winterHours}
                            </p>
                            <p className="address mb-3">
                              <em>
                                  <strong>{storeInfo.address.street}</strong>
                                  <br />
                                  {storeInfo.address.city}, {storeInfo.address.state} {storeInfo.address.zip}
                              </em>
                            </p>
                            <p className="address mb-3">
                              <a aria-label="find us on google" title="find us on google" class="icon-link" href="https://www.google.com/search?q=sunburst+pool+43062">
                                <GoogleLogoIcon/>
                              </a> 
                              <a aria-label="find us on facebook" title="find us on facebook" class="icon-link" href="https://maps.app.goo.gl/Dj6RDpQPiRv8PEfJ9">
                                <FacebookLogoIcon/>
                              </a> 
                              <a aria-label="find us on google maps" title="find us on google maps" class="icon-link" href="https://maps.app.goo.gl/Dj6RDpQPiRv8PEfJ9">
                                <GoogleMapsLogoIcon/>
                              </a>
                            </p>
                            <p className="mb-0">
                              <strong>{storeInfo.phone.title}</strong>
                              <br />
                              {storeInfo.phone.number}
                              <br />
                              {storeInfo.email}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
  </Layout>
)
    }

export default StorePage

export const pageQuery = graphql`
query StoreDetails {
    allMarkdownRemark(
      filter: {frontmatter: {type: {eq: "data"}, for: {eq: "storeInfo"}}}
    ) {
      nodes {
        id
        frontmatter {
          title
          email
          subtitle
          hours {
            day
            time
          }
          winterHours
          address {
            city
            state
            street
            zip
          }
          phone {
            number
            title
          }
        }
      }
    }
  }
` 